.gpt3__possibility {
    display: flex;
    flex-direction: row;
}

.gpt3__possibility-image {
    /* flex: 1; */
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 16px;
    margin-right: 3rem;
}

/* .gpt3__possibility-image img {
    width: 300px;
    height: auto;
} */

.gpt3__possibility-content {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.gpt3__possibility-content a {
    text-decoration: line-through;
}


.gpt3__possibility-content h4 {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 30px;
    color: #71e5ff;
}

.gpt3__possibility-content h4:last-child {
    color: #ff8a71;
}

.gpt3__possibility-content h1 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 34px;
    line-height: 45px;
    margin: 1rem 0;
}

.gpt3__possibility-content p {
    font-family: var(--font-family);
    font-size: 16px;
    line-height: 30px;
    color: var(--color-text);
    margin-bottom: 2rem;
}
.about__gallery-column {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 16px;
}
.about__gallery-column img {
    object-fit: cover;
}

@media screen and (max-width: 950px) {
    .gpt3__possibility {
        flex-direction: column;
    }
    .gpt3__possibility-image {
        margin: 1rem 0;
    }
    .gpt3__possibility-content {
        margin-top: 2rem;
    }
}