.article-left h1 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 34px;
    line-height: 45px;
    margin: 1rem 1;   
}

.article-left p {
    margin: 1rem 0;
    
}

.article-left {
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    gap: 4rem;
    align-items: center;
}

.articleL__description {
    width: 50%;
    text-align: right;
}

.articleL__image {
    width: 50%;
    text-align: center;
}
.articleL__image img {
    width: 100%;

}